import { defineComponent, ref, computed } from 'vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import { NDropdown, NIcon, useDialog, useMessage } from 'naive-ui';
import { AddSharp } from '@vicons/ionicons5';
import dayjs from 'dayjs';
import ModalComponent from '@/components/Modal.vue';
export default defineComponent({
    name: 'PageTheme',
    components: {
        NDropdown,
        NIcon,
        AddSharp,
        ModalComponent
    },
    setup() {
        const dialog = useDialog();
        const message = useMessage();
        const store = useStore();
        const router = useRouter();
        // store.dispatch('init')
        const defaultColor = {
            '--van-red': '#ee0a24',
            '--van-blue': '#1989fa',
            '--van-orange': '#ff976a',
            '--van-orange-dark': '#ed6a0c',
            '--van-orange-light': '#fffbe8',
            '--van-green': '#07c160'
        };
        const themeList = computed(() => {
            const list = [...store.state.themeUserConfig];
            list.map((item) => {
                item.newTheme = {
                    ...item.newTheme
                };
                item.update = dayjs(item.update).format('YYYY-MM-DD HH:mm');
                return item;
            });
            return list;
        });
        const showModal = ref(false);
        const options = [
            {
                label: '查看主题',
                key: 'see'
            },
            {
                label: '修改命名',
                key: 'edit'
            },
            {
                label: '复制主题',
                key: 'copy'
            },
            {
                label: '删除主题',
                key: 'delete'
            },
            {
                label: '下载主题文件',
                key: 'dowalond'
            }
        ];
        let optionType = '';
        let currentTheme = {};
        const useModalOpen = (type) => {
            optionType = type;
            showModal.value = true;
        };
        const useModalClose = () => {
            showModal.value = false;
        };
        const handleSelect = (key, theme) => {
            optionType = key;
            currentTheme = theme;
            switch (key) {
                case 'see':
                    store.dispatch('useTheme', theme);
                    router.push('/preview');
                    break;
                case 'edit':
                    useModalOpen(key);
                    break;
                case 'copy':
                    useModalOpen(key);
                    break;
                case 'delete':
                    dialog.error({
                        title: '我是一个提醒',
                        content: '真的要删除我咩？o(╥﹏╥)o',
                        positiveText: '狠心删除',
                        negativeText: '不删了',
                        maskClosable: false,
                        onPositiveClick: () => {
                            store.dispatch('deleteTheme', currentTheme.id);
                            message.success('emmm，行吧，再见啦👋');
                        }
                    });
                    break;
                case 'dowalond':
                    store.dispatch('downloadTheme', currentTheme);
            }
        };
        const useModalConfirm = ({ nameValue: name }) => {
            const type = optionType;
            switch (type) {
                case 'add':
                    store.dispatch('addTheme', { name, type: 'add' });
                    router.push('/preview');
                    break;
                case 'edit':
                    store.dispatch('updateTheme', {
                        id: currentTheme.id,
                        name,
                        newTheme: currentTheme.newTheme,
                        oldTheme: currentTheme.oldTheme,
                        update: currentTheme.update
                    });
                    break;
                case 'copy':
                    store.dispatch('addTheme', {
                        name,
                        newTheme: currentTheme.newTheme,
                        oldTheme: currentTheme.oldTheme,
                        type: 'copy'
                    });
                    break;
            }
        };
        return {
            defaultColor,
            themeList,
            options,
            showModal,
            useModalOpen,
            useModalClose,
            useModalConfirm,
            handleSelect
        };
    }
});
